<template>
    <li @click="handleCheck(l.id)">
        <label>
            <input type="checkbox" :checked="l.done"  />
            <span>{{l.title}}</span> 
        </label>
        <button @click="handleDelete">删除</button>
    </li>
</template>

<script>
export default {
    name : "MyItem",
    props: ["l","deleteList"],
    methods:{
        handleCheck(){
            this.l.done = !this.l.done;
        },
        handleDelete(e) {
            // console.log(e );
            e.stopPropagation();
            // this.deleteList(this.l.id);
            this.$bus.$emit("deleteList",this.l.id);
        }
    },
 
}
</script>

<style lang="scss" scoped>
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
li {
    width: 80vw;
    margin: 0 auto;
    list-style: none;
    display: flex;
    background: lightblue;
    margin-top: 10px;
    padding:20px;
    justify-content: space-between;
    
    border-radius: 20px;
    font-size: 30px;
    color: green;
    font-weight: bold;
    cursor: pointer;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    label input{
       font-size: 100px;
       margin-right: 40px;
    }
    &:hover {
        background: pink;
        button {
            display: inline-block;
            cursor: pointer;
        }
    }
    button {
        background: rgb(211, 40, 40);
        color: rgb(204, 192, 192);
        padding: 8px;
        border: none;
        outline: none;
        border-radius: 10px;
        display: none;
        &:hover {
            background: rgb(253, 6, 6);
            color: white;
        }
    }
}

</style>