<template>
	<div class="list-header">
		<input
			type="text"
			placeholder="输入任务名，回车确认"
			v-model="title"
			@keyup.enter="add"
		/>
	</div>
</template>

<script>
	import { nanoid } from "nanoid";
	export default {
		name: "MyHeader",
		props: ["addList",'lists'],
		data() {
			return {
				title: "",
			};
		},
		methods: {
			add() {
				//校验输入数据
				if (!this.title.trim()) return alert("输入不能为空");
				const listObj= {
					id: nanoid(),
					title: this.title,
					done: false,
				};
				this.addList(listObj)
				this.title = ""
			},
		},
	};
</script>

<style lang="scss" scoped>
	$bg:rgba(167, 169, 41, 0.1);
	.list-header {
		margin-bottom: 20px;
		input{
			box-shadow: 9px 5px 8px 3px rgba(30, 27, 27, .9);
			font-size: 30px;
			color: rgb(69, 58, 58);
			border: none;
			line-height: 80px;
			font-weight:bold;
			background: 10%$bg;
			outline: #ccc;
			border-radius: 10px;
			text-align: center;
		}
	}
	
</style>