<template>
	<div id="app">
		<h1>todoList</h1>
		<MyHeader :addList="addList" :lists="lists"></MyHeader>
		<MyList :checkList="checkList" :lists="lists"></MyList>
		<MyFooter :checkAll="checkAll" :lists="lists" :clearAll="clearAll"></MyFooter>
	</div>
</template>
<script>
import MyHeader from "./components/MyHeader";
import MyFooter from "./components/MyFooter";
import MyList from "./components/MyList";
export default {
	name: "App",
	components: { MyHeader, MyFooter, MyList },
	data() {
		return {
			lists: JSON.parse(localStorage.getItem("lists")) || []
		};
	},
	methods: {
		addList(listObj) {
			const arr = [];
			this.lists.forEach((l) => {
				arr.push(l.title);
			});
			if (arr.includes(listObj.title)) {
				alert(`${listObj.title}已存在`);
				return;
			}
			this.lists.unshift(listObj);
		},
		checkList() {
			this.lists.forEach((list) => {
				list.done = !list.done;
			});
		},
		checkAll(value) {
			this.lists.forEach((l) => {
				l.done = value
			})
		},
		clearAll() {
			let isPresent = this.lists.filter((l) => l.done == true);
			if (isPresent.length) {
				confirm(`确定删除选中?`) ?
					this.lists = this.lists.filter((l) => l.done == false) : ''
			} else {
				alert("未选中！！！")
			}
		},
		deleteList(id) {
			confirm("确定删除这一项？") ?
				this.lists = this.lists.filter((l) => l.id !== id) : ''
		}
	},
	watch: {
		lists: {
			deep: true,
			handler(value) {
				localStorage.setItem('lists', JSON.stringify(value));
			}
		}
	},
	mounted() {
		this.$bus.$on('deleteList', this.deleteList)
		this.$bus.$on('checkAll', this.checkAll)
	},
	beforeDestroy() {
		this.$bus.$off('deleteList')
		this.$bus.$off('checkAll')
	}

};
</script>

