<template>
    <div class="lis-footer">
        <label>
            <input type="checkbox" v-model="isAll">全选
            <span>已选{{doneList}}/{{total}}</span>
            <button @click="clearAll">清除</button>
        </label>
    </div>
</template>

<script>
export default {
    name: "MyFooter",
    props: ["clearAll","lists"],
    computed: {
        total(){
            return this.lists.length
        },
        doneList(){
            return this.lists.reduce((pre,list)=>{
                return pre + (list.done ? 1 : 0 )
            },0)
        },
        isAll: {
            get(){
                return this.doneList === this.total && this.total > 0
            },
            set(value){
                this.$bus.$emit('checkAll',value)
            }
        },
    }
    

}
</script>

<style lang="scss" scoped>
    .lis-footer {
        background:#ccc;
        display: flex;
        justify-content: center;
        width: 100%;
        position: fixed;
        bottom: 0;
        left: 0;
        button{
            padding:10px;
            margin-left: 10px;
            border: none ;
            cursor: pointer;
            color: rgb(34, 30, 30);
            background: rgb(206, 84, 84) ;
            span{
                display: inline-block;
            }
        }
    }
</style>